import React from "react";
import { theme, getTextDimensions, styled } from "@faharmony/theme";
import { Grid, Icon, Text, Tooltip } from "@faharmony/components";
import { IBreadcrumb, IBreadcrumbsProps } from "./types";

const maxCrumbWidth = 200;

interface ICrumb {
  crumb: IBreadcrumb;
  lastCrumb?: boolean;
  separator?: string;
  variant?: IBreadcrumbsProps["variant"];
}

const StyledCrumb = styled(Grid)<ICrumb>`
  color: ${(p) =>
    p.crumb.onClick
      ? theme.color.text.secondary
      : p.lastCrumb
      ? theme.color.text.primary
      : theme.color.text.disabled};
  cursor: ${(p) => (p.crumb.onClick ? "pointer" : "initial")};
  max-width: ${maxCrumbWidth}px;
  :hover {
    color: ${(p) =>
      p.crumb.onClick || p.lastCrumb
        ? theme.color.text.primary
        : theme.color.text.disabled};
  }
`;

const Crumb = ({
  crumb,
  lastCrumb = false,
  variant = "p",
  separator = "/",
}: ICrumb) => {
  const { id, icon, label, onClick, tooltip } = crumb;
  const showTooltip = tooltip
    ? true
    : label
    ? getTextDimensions(label, variant).width > maxCrumbWidth - 20
    : false;

  if (icon || label) {
    const item = (
      <StyledCrumb
        onClick={onClick}
        width="auto"
        role="breadcrumb"
        crumb={crumb}
        lastCrumb={lastCrumb}
        spacing="sm"
        gridTemplate="auto / repeat(3, max-content)"
      >
        {icon && <Icon icon={icon} />}
        {label && <Text value={label} variant={variant} truncate />}
        {(!lastCrumb || onClick) && (
          <Text value={` ${separator} `} variant={variant} color="disabled" />
        )}
      </StyledCrumb>
    );
    if (showTooltip)
      return <Tooltip value={tooltip || label || ""}>{item}</Tooltip>;
    else return item;
  }
  console.warn(
    `Crumb ${id} could not be rendered since there is no label or icon.`
  );
  return null;
};

export default Crumb;
