import { styled } from "@faharmony/theme";
import { Theme, theme } from "@faharmony/theme";
import { IStyleProps } from "./types";

const minHeight = 20;
const baseHeight = 32;

const calcHeight = ({ spacing = "md" }: IStyleProps) =>
  ["lg"].includes(spacing)
    ? baseHeight + 4
    : ["xl"].includes(spacing)
    ? baseHeight + 8
    : ["sm"].includes(spacing)
    ? baseHeight - 4
    : ["xs"].includes(spacing)
    ? baseHeight - 8
    : ["none"].includes(spacing)
    ? minHeight
    : baseHeight;

const calcPadding = (props: IStyleProps) =>
  Math.max(calcHeight(props) - minHeight - 2, 0) / 2;

const calcBackgroundColor = ({ variant, isOutline }: IStyleProps) => {
  if (isOutline) return "transparent";
  switch (variant) {
    case "primary":
      return Theme.colorBackground("inverse");
    case "secondary":
      return Theme.colorBackground("elevated");
    case "default":
      return Theme.colorBackground("base");
    default:
      return Theme.colorTint(variant);
  }
};

const calcOutlineColor = ({ variant }: IStyleProps) => {
  switch (variant) {
    case "primary":
      return Theme.colorBackground("inverse");
    case "secondary":
      return Theme.colorOutline();
    case "default":
      return Theme.colorOutline();
    default:
      return Theme.colorTint(variant);
  }
};

const calcTextColor = (props: IStyleProps) => {
  const { variant, isOutline } = props;
  if (isOutline) {
    switch (variant) {
      case "secondary":
        return Theme.colorText("primary");
      case "default":
        return Theme.colorText("secondary");
      default:
        return calcOutlineColor(props);
    }
  }
  switch (variant) {
    case "primary":
      return Theme.colorText("inverse");
    case "secondary":
      return Theme.colorText("primary");
    case "default":
      return Theme.colorText("secondary");
    default:
      return Theme.colorTint("white");
  }
};

const StyledBaseButton = styled.button<IStyleProps>`
  margin: 0px;

  overflow: hidden;
  padding: ${calcPadding}px;
  height: ${calcHeight}px;
  width: ${(props) => (props.fullWidth ? "fill-available" : "fit-content")};
  min-width: ${calcHeight}px;

  background-color: transparent;
  background: ${calcBackgroundColor};
  border-radius: ${theme.radius.sm};
  opacity: ${(props) => (props.disabled ? 0.5 : 1)};
  box-shadow: ${(props) => (props.isOutline ? "none" : Theme.shadow())};
  border: 1px solid
    ${(props) =>
      props.isOutline ? calcOutlineColor(props) : calcBackgroundColor(props)};

  cursor: ${(props) =>
    props.disabled
      ? "not-allowed"
      : props.isLoading === true
      ? "wait"
      : "pointer"};
  user-select: none;
  text-align: center;
  color: ${calcTextColor};
  filter: none;

  :hover {
    box-shadow: ${(props) =>
      props.disabled || props.isLoading ? "" : Theme.shadow("hover")};
  }

  :active {
    box-shadow: ${(props) =>
      props.disabled || props.isLoading ? "" : Theme.shadow("hover")};
    filter: ${(props) =>
      props.disabled || props.isLoading ? "" : "brightness(80%)"};
  }
  :focus,
  :focus-within {
    box-shadow: ${(props) =>
      props.disabled || props.isLoading ? "" : Theme.shadow("hover")};
    filter: ${(props) =>
      props.disabled || props.isLoading ? "" : "brightness(120%)"};
  }
`;

export const StyledButton = styled(StyledBaseButton)<IStyleProps>`
  .p,
  .h5 {
    text-align: center;
    vertical-align: middle;
    height: 24px;
    padding: 0 4px;
  }
`;

export const StyledIconButton = styled(StyledBaseButton)<IStyleProps>`
  max-width: ${calcHeight}px;
`;
