import React, { forwardRef } from "react";
// import { Text } from "../Text";
import { Icon } from "../Icon";
import { Box } from "../Box";
import { Tooltip } from "../Tooltip";
import { IButtonProps, IStyleProps } from "./types";
import { StyledButton, StyledIconButton } from "./style";
import { faSpinner } from "@faharmony/icons";

/**
 * Button
 * ---
 * A clickable button with multiple states, sizes and style variants.
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 * @version 1.6.0
 * @example
 * <Button value="Notifications" icon={faBell} />
 */
const Button = forwardRef<HTMLButtonElement, IButtonProps>((props, ref) => {
  const {
    id,
    className,
    value,
    children,
    tooltip,
    onClick,
    onContextMenu,
    spacing = "md",
    disabled = false,
    variant = "default",
    outline: isOutline = false,
    ghost: isGhost = false,
    loading: isLoading = false,
    loadingText,
    type = "button",
    onFocus,
    onBlur,
  } = props;

  const name = props.name || value || tooltip || "";
  const icon = isLoading ? faSpinner : props.icon;

  if (isGhost && isOutline) {
    // WARNING: Button styled is conflicting.
    console.warn(
      `Button (${name}) could be either Outline-styled or Ghost-styled, not both.`
    );
    return null;
  }
  if (!value && !children && !icon) {
    // WARNING: Button is empty.
    console.warn(
      `Button (${name}) could not be rendered since content (icon/value/children) is not provided.`
    );
    return null;
  }

  // IconButton test
  const isIconButton = icon && !value && !children;

  const styleProps: IStyleProps = {
    disabled,
    fullWidth: isIconButton ? false : props.fullWidth,
    isLoading,
    spacing,
    isOutline,
    variant,
  };

  const buttonProps = {
    id,
    ref,
    role: "button",
    type,
    name,
    className: "Button button " + className,
    onClick: disabled || isLoading ? undefined : onClick,
    onContextMenu: disabled || isLoading ? undefined : onContextMenu,
    onFocus,
    onBlur,
  };

  const visibleText = isLoading && loadingText ? `${loadingText}...` : value;
  const button = isIconButton ? (
    <StyledIconButton {...buttonProps} {...styleProps}>
      <Icon icon={icon} spin={isLoading} />
    </StyledIconButton>
  ) : (
    <StyledButton {...buttonProps} {...styleProps}>
      {children || (
        <Box spacing="sm" justifyContent="center">
          {(icon || isLoading) && <Icon icon={icon} spin={isLoading} />}
          {visibleText && (
            <div
              className={variant === "primary" ? "h5" : "p"}
              style={{
                whiteSpace: "nowrap",
                overflow: "hidden",
                textOverflow: "ellipsis",
              }}
            >
              {visibleText}
            </div>
          )}
        </Box>
      )}
    </StyledButton>
  );

  return tooltip ? (
    <Tooltip value={tooltip} disabled={disabled}>
      {button}
    </Tooltip>
  ) : (
    button
  );
});

export default Button;
