import React, { useMemo } from "react";
import { faEllipsisH, faArrowLeft } from "@faharmony/icons";
import { usePropState } from "@faharmony/helpers";
import { Box, Button } from "@faharmony/components";
import { IBreadcrumb, IBreadcrumbsProps } from "./types";
import Crumb from "./Crumb";

const useBreadcrumbsCollapse = (
  crumbList: IBreadcrumb[],
  collapseThreshold: number = 4
) => {
  const [crumbs] = usePropState(crumbList);
  const [isCollapsed, setCollapsed] = usePropState(
    crumbs.length > collapseThreshold
  );

  const breadcrumbs = useMemo(() => {
    if (isCollapsed)
      return [
        crumbs[0],
        {
          id: "collapsed",
          icon: faEllipsisH,
          tooltip: `View ${crumbs.length - 2} collapsed crumbs`,
          onClick: () => {
            setCollapsed(false);
          },
        },
        crumbs[crumbs.length - 1],
      ];
    else return crumbs;
  }, [crumbs, isCollapsed]);

  return breadcrumbs;
};

/**
 * Breadcrumbs
 * ---
 * Renders a breadcrumb list with a possibility of back button.
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 * @version 1.6.0
 * @example
 * <Breadcrumbs crumbs={[{
 *    id: 'crumb1'
 *    label: 'Crumb 1',
 *    icon: 'faLink'
 *    onClick: () => {},
 *    tooltip: "This is a crumb"
 * }]} />
 */
const Breadcrumbs = (props: IBreadcrumbsProps) => {
  const crumbs = useBreadcrumbsCollapse(props.crumbs, props.collapseThreshold);

  const renderedCrumbs = React.useMemo(
    () => (
      <Box wrap={crumbs.length > 4 ? "wrap" : "nowrap"} spacing="sm">
        {crumbs.map((crumb, index) => (
          <Crumb
            key={crumb.id}
            crumb={crumb}
            variant={props.variant || "h5"}
            separator={props.separator || "/"}
            lastCrumb={index === crumbs.length - 1}
          />
        ))}
      </Box>
    ),
    [crumbs]
  );

  if (crumbs.length > 0)
    if (props.onClickBackButton) {
      return (
        <Box justifyContent="start">
          <Button
            icon={faArrowLeft}
            tooltip={"Back"}
            spacing="xs"
            variant="secondary"
            disabled={crumbs.length === 1}
            onClick={props.onClickBackButton}
          />
          {renderedCrumbs}
        </Box>
      );
    } else return renderedCrumbs;
  else {
    console.warn(
      "Breadcrumbs could not be rendered since no crumbs were passed."
    );
    return null;
  }
};

export { Breadcrumbs, IBreadcrumb, IBreadcrumbsProps };
