import React from "react";
import { styled, Theme } from "@faharmony/theme";
import {
  FALogoIconDefinition,
  IconDefinition,
  FontAwesomeIcon,
  FontAwesomeIconProps,
} from "@faharmony/icons";
import { Box } from "../Box";

interface IIconProps {
  /** FontAwesome Icon object (needs to be imported from icons library). */
  icon?: IconDefinition;
  /** Color of the Icon. Accepts all CSS value + Theme variables @default 'inherit' */
  color?: string;
  /** Size of icon in relation to font-size.
   * Rendered icon is has custom dimensions until the size argument is passed.
   * @default Custom */
  size?: FontAwesomeIconProps["size"];
  /** Rotate in icon (degrees) - 0, 90, 180, 270 @default 0 */
  rotate?: FontAwesomeIconProps["rotate"];
  /** Flip the icon in one or both directions */
  flip?: FontAwesomeIconProps["flip"];
  /** Spin the icon in clockwise directions (good for loaders) */
  spin?: FontAwesomeIconProps["spin"];
  /** Blink the icon in clockwise directions (good for loaders) */
  pulse?: FontAwesomeIconProps["pulse"];
  /** Function to execute when clicked. */
  onClick?: (
    event?: React.MouseEvent<Element, MouseEvent> | React.TouchEvent<Element>
  ) => any;
  /** Add a class name to the component */
  className?: string;
}

const StyledIcon = styled(Box)`
  display: inline-block;
  /* width: ${() => `calc(${Theme.spacing("xl")} + ${Theme.spacing("sm")})`};
  height: ${() => `calc(${Theme.spacing("xl")} + ${Theme.spacing("sm")})`}; */  
  width: 20px;
  height: 20px;
  font-size: ${() => Theme.spacing("xl")};
  text-align: center;
  
`;

/**
 * Icon
 * ---
 * Render an icon from FontAwesome library. By default, renders FA logo icon.
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 * @version 1.6.0
 * @example
 * <Icon icon="faFolders" />
 * <Icon icon="faSpinner" spin />
 */
const Icon = React.forwardRef<HTMLDivElement, IIconProps>((props, ref) => {
  const fontAwesomeIconProps: FontAwesomeIconProps = {
    icon: props.icon || FALogoIconDefinition,
    size: props.size,
    rotate: props.rotate,
    flip: props.flip,
    spin: props.spin,
    pulse: props.pulse,
    color: props.color,
    onClick: props.onClick,
    role: "icon",
    fixedWidth: props.size ? false : true,
  };
  const fontAwesomeIcon = (
    <FontAwesomeIcon {...fontAwesomeIconProps} className={"iconSVG"} />
  );

  return props.size ? (
    fontAwesomeIcon
  ) : (
    <StyledIcon ref={ref} className={"Icon " + (props.className || "")}>
      {fontAwesomeIcon}
    </StyledIcon>
  );
});

export { Icon, IIconProps };
